import BaseBoard from '../Components/BaseBoard';
import WorkCard from '../Components/WorkCard';
import './Works.css';
import { motion } from 'framer-motion';
import workList from './data/workList';

const Works = () => {
    const items = [];
    workList.forEach((item, i) => {
        items.push(
            <motion.div
                initial={{
                    marginTop: -32,
                    opacity: 0,
                }}
                animate={{
                    marginTop: 0,
                    opacity: 1,
                }}
                transition={{
                    duration: 0.5,
                    delay: i * 0.03,
                    ease: 'easeOut',
                }}
            >
                <WorkCard type={item.type} content={item.content} description={item.description} />
            </motion.div>
        );
    });

    return (
        <BaseBoard title={'HOME > PRODUCTS'}>
            <div className="ProductDescription"></div>
            <div className="ProductPage">{items}</div>
        </BaseBoard>
    );
};

export default Works;
