import './BaseBoard.css';
import VBase from '../Wrapper/VBase';
import Button from '../Button';
import OverlappedMenu from '../OverlappedMenu';
import ProfileChip from '../ProfileChip';
import { useState } from 'react';
import TitleView from '../TitleView';

const BaseBoard = (props) => {
    const [isShowOverlappedMenu, setShowOverlappedMenu] = useState(false);

    const setShowOverlappedMenuFunc = () => {
        setShowOverlappedMenu((value) => !value);
    };

    return (
        <VBase onPressEsc={setShowOverlappedMenuFunc}>
            <TitleView name={props.title} />
            <div className="BaseBoard All">
                <div className="BaseBoard Main">{props.children}</div>
                <div className="BaseBoard SubFooter" />
                <div className="BaseBoard Footer">copyright (c)2022-2022 YA7GI All rights reserved.</div>
                <div className="BaseBoard Menu">
                    <ProfileChip />
                    <Button title="Menu" subj="line" onClick={setShowOverlappedMenuFunc} />
                </div>
                {isShowOverlappedMenu ? <OverlappedMenu onClick={setShowOverlappedMenuFunc} /> : null}
            </div>
        </VBase>
    );
};

export default BaseBoard;
