import { useRef } from 'react';
import './WorkCard.css';

const WorkCard = (props) => {
    const mainRef = useRef();
    const contentComponent = () => {
        switch (props.type) {
            case 'ILLUST':
            case 'ANIMATION':
                return (
                    <div>
                        <div className="WorkCardContentIllust">
                            <img src={props.content} />
                        </div>
                        <div className="WorkCardTypeLabel">
                            <div className="WorkCardTypeLabelIcon" style={getTypeColor(props.type)}>
                                {props.type}
                            </div>
                        </div>
                        <div className="WorkCardTypeDescription">{props.description}</div>
                    </div>
                );
        }
    };

    const getTypeColor = (type) => {
        switch (type) {
            case 'ILLUST':
                return { backgroundColor: '#eedd00' };
            case 'ANIMATION':
                return { backgroundColor: '#ff0044' };
        }
    };

    return (
        <div className="WorkCard">
            <div className="WorkCardMain" ref={mainRef}>
                {contentComponent()}
            </div>
        </div>
    );
};

export default WorkCard;
