import { useCallback, useEffect } from 'react';

const VBase = (props) => {
    const { children, onPressEsc } = props;
    const escFunc = useCallback((e) => {
        if (e.keyCode === 27) {
            onPressEsc();
        }
    });

    useEffect(() => {
        document.addEventListener('keydown', escFunc, false);
    }, []);

    return <div>{children}</div>;
};

export default VBase;
