import Button from '../Button';
import { motion } from 'framer-motion';
import { CloseSquareOutlined } from '@ant-design/icons';
import './OverlappedMenu.css';
import { Link } from 'react-router-dom';
import paths from '../../links';

const OverlappedMenu = (props) => {
    const animateParam = {
        marginTop: 0,
        opacity: 1,
    };
    const transitionParam = (delay = 0) => {
        return {
            duration: 0.2,
            ease: 'circOut',
            delay: 0.1 + delay,
        };
    };

    return (
        <motion.div
            animate={{
                backgroundColor: '#000a',
            }}
            transition={{ duration: 0.15 }}
            className="OverlappedMenu"
        >
            <div className="OverlappedMenuContainar">
                <div className="OverlappedMenuCloseIconContainer">
                    <div className="OverlappedMenuCloseIconSpace" />
                    <CloseSquareOutlined className="OverlappedMenuCloseIcon" onClick={props.onClick} />
                </div>
                <motion.div
                    animate={{
                        opacity: 1,
                        marginTop: 0,
                    }}
                    transition={{ duration: 0.2, ease: 'circOut' }}
                    className="OverlappedMenuTitle"
                >
                    <p>Menu</p>
                </motion.div>
                <div className="OverlappedMenuButtons">
                    <Link to={paths.root} style={{ textDecoration: 'none' }}>
                        <motion.div
                            style={{ marginTop: -50, opacity: 0 }}
                            animate={animateParam}
                            transition={transitionParam()}
                        >
                            <Button title="Home" subj="line" />
                        </motion.div>
                    </Link>
                    <Link to={paths.work} style={{ textDecoration: 'none' }}>
                        <motion.div
                            style={{ marginTop: -50, opacity: 0 }}
                            animate={animateParam}
                            transition={transitionParam(0.05)}
                        >
                            <Button title="Products" subj="line" />
                        </motion.div>
                    </Link>
                </div>
            </div>
        </motion.div>
    );
};

export default OverlappedMenu;
