import { useState } from 'react';
import { motion } from 'framer-motion';
import './Button.css';

const Button = (props) => {
    const [isHover, setIsHover] = useState(false);

    const setLightColor = () => {
        switch (props.type) {
            case 'primary':
                return isHover ? '#ffcc3f' : '#FFFBEB';
            case 'secondary':
                return isHover ? '#ff3f72' : '#FFFBEB';
            default:
                return isHover ? '#229fff' : '#FFFBEB';
        }
    };

    const setTextColor = () => {
        switch (props.type) {
            case 'primary':
                return isHover ? '#7a3b00' : '#FFFBEB';
            case 'secondary':
                return isHover ? '#5c043c' : '#FFFBEB';
            default:
                return isHover ? '#1f1685' : '#FFFBEB';
        }
    };

    const stickerButton = () => {
        return (
            <motion.div
                className="VButton"
                onMouseEnter={() => setIsHover(true)}
                onMouseLeave={() => setIsHover(false)}
                onClick={props.onClick}
                animate={{
                    opacity: 1.0,
                }}
                transition={{ duration: 0.1 }}
            >
                <motion.div
                    animate={{
                        width: isHover ? '100%' : 0,
                        backgroundColor: setLightColor(),
                    }}
                    transition={{ duration: 0.15 }}
                    className="line"
                />
                <motion.div
                    className="title"
                    animate={{
                        color: setTextColor(),
                    }}
                    transition={{ duration: 0.1 }}
                >
                    {props.title}
                </motion.div>
            </motion.div>
        );
    };
    const lineButton = () => {
        return (
            <motion.div
                className="LineButton"
                onMouseEnter={() => setIsHover(true)}
                onMouseLeave={() => setIsHover(false)}
                onClick={props.onClick}
                animate={{
                    opacity: 1.0,
                }}
                transition={{ duration: 0.1 }}
            >
                <motion.div
                    animate={{
                        width: isHover ? '100%' : '0%',
                        backgroundColor: setLightColor(),
                    }}
                    transition={{ duration: 0.15 }}
                    className="LineLine"
                />
                <motion.div
                    className="LineTitle"
                    animate={{
                        color: setLightColor(),
                    }}
                    transition={{
                        duration: 0.1,
                        ease: 'easeOut',
                    }}
                >
                    {props.title}
                </motion.div>
            </motion.div>
        );
    };

    switch (props.subj) {
        case 'line':
            return lineButton();
        default:
            return stickerButton();
    }
};

export default Button;
