import { useState } from 'react';
import { motion } from 'framer-motion';
import { Tooltip } from 'antd';

const LinkIcon = (props) => {
    const {
        Icon,
        onClick = () => {},
        bgHoverColor = '#ff00aa',
        bgIdleColor = '#263159',
        bdHoverColor = '#111',
        bdIdleColor = '#263159',
        ttHoverColor = '#fff',
        ttIdleColor = '#fff',
        transition = { duration: 0.2 },
        isMini = false,
        label = 'hoge',
        tips = null,
    } = props;
    const [isTouch, setTouch] = useState(false);

    const mainComponent = (
        <div className="ProfileLinkIconListFloatLeft">
            <motion.div
                className={isMini ? 'MiniProfileCardLink' : 'ProfileCardLink'}
                onMouseEnter={() => {
                    setTouch(true);
                }}
                onMouseLeave={() => {
                    setTouch(false);
                }}
                onClick={() => {
                    onClick();
                }}
                animate={{
                    backgroundColor: isTouch || isMini ? bgHoverColor : bgIdleColor,
                    borderColor: isTouch || isMini ? bdHoverColor : bdIdleColor,
                    color: isTouch || isMini ? ttHoverColor : ttIdleColor,
                }}
                transition={transition}
            >
                {Icon}
                {isMini ? null : <p>{label}</p>}
            </motion.div>
        </div>
    );

    if (tips != null) {
        return <Tooltip title={tips}>{mainComponent}</Tooltip>;
    } else {
        return mainComponent;
    }
};

export default LinkIcon;
